import moment from "moment";
import { useNavigate } from "react-router-dom";
import Service from "../../API/Service";
import { useQuery } from "react-query";
import { Box, Button, Chip, Divider, Grid, Stack, Typography } from "@mui/material";
import MainCard from "components/MainCard";

function StationItem({ station }) {
    const navigate = useNavigate();

    const { data } = useQuery(
        ['lastActive', station._id], () => Service.getLastActive(station._id),
        {
            select: (data) => {
                return data?.lastActive
            },
            refetchOnWindowFocus: false,
            refetchInterval: 1000,
        }
    );


    const dateParse = (date) => {
        const dateX = moment(Number(date)).format('DD.MM.YYYY, HH:mm:ss');
        return dateX;
    }

    const onClickHandler = (id) => {
        navigate("/devices", { state: id });
    }

    return (
        <Grid item xs={14} sm={8} md={5} lg={4}>
            <MainCard contentSX={{ p: 2.25 }} >
                <Box>
                    <Grid container sx={{ mb: 1.5 }}>
                        <Grid item xs>
                            <Typography variant="h3">{station.name || 'Название не задано'}</Typography>
                        </Grid>
                        <Grid item alignContent={"right"}>
                            <Chip label={data?.activeBool ? "Подключен" : "Отключен"} variant="contained" color={data?.activeBool ? "success" : "error"} />
                        </Grid>
                    </Grid>
                </Box>
                <Divider component="div" primary="dark" />
                <Stack sx={{ mt: 1.5 }} spacing={1}>
                    <Typography variant="h6">{"Интервал опроса: " + station.interval}</Typography>
                    <Typography variant="h6">{"Расположение: " + station.placeName}</Typography>
                    <Typography variant="h6">{"Компания: " + station.company}</Typography>
                    <Typography variant="h6">{`Последний опрос:  ${(data?.lastActive && dateParse(data?.lastActive)) || "Загрузка..."}`}</Typography>
                    <Button
                        size="medium"
                        type="submit"
                        variant="contained"
                        color="primary"
                        // disabled={!data?.activeBool}
                        onClick={() => onClickHandler(station?._id)}
                    >
                        Подробнее
                    </Button>
                </Stack>

            </MainCard>
        </Grid >
    );
}

export default StationItem;