import { useEffect, useState } from "react";
import Service from "../../API/Service";
import { useFetching } from "../../utils/hooks/useFetching";
import StationItem from "./StationItem";
function StationList() {
    const [stations, setStations] = useState([])
    const [fetchStation, isStatationLoading, stationError] = useFetching(async () => {
        const response = await Service.getStation();
        setStations(response.station);
    })

    useEffect(() => {
        fetchStation();
    }, [])



    if (isStatationLoading) return <h1>Загрузка...</h1>;
    if (stationError) return <h1>{stationError}</h1>;
    return (
        <>
            {
                stations.map((station) => {
                    return (
                        <StationItem
                            key={station._id}
                            station={station}
                        />
                    );
                })
            }
        </>
    );
}

export default StationList;